/* Container */
.wishlist-page {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}

/* Heading */
.wishlist-title {
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  color: #1f2937;
}

/* Empty state */
.wishlist-empty {
  text-align: left;
  font-size: 1.05rem;
  color: #6b7280;
}

/* Grid */
.wishlist-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 1.25rem;
}

/* Card */
.wishlist-card {
  background: #fff;
  border: 1px solid rgba(0,0,0,.08);
  border-radius: 12px;
  box-shadow: 0 4px 12px rgba(0,0,0,.05);
  overflow: hidden;
  transition: transform .15s ease, box-shadow .15s ease;
}
.wishlist-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0,0,0,.1);
}

/* Image */
.wishlist-image {
  width: 100%;
  height: 180px;
  object-fit: cover;
  display: block;
}

/* Info */
.wishlist-info {
  padding: 1rem;
}
.wishlist-name {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: .4rem;
  color: #111827;
}
.wishlist-price {
  font-size: 1rem;
  font-weight: 600;
  color: #f97316;
  margin-bottom: .9rem;
}

/* Actions */
.wishlist-actions {
  display: flex;
  gap: .5rem; 
}
.wishlist-btn { 
  flex: 1;
  padding: .5rem;
  font-size: .95rem;
  font-weight: 600;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: background .15s ease, color .15s ease;
}

/* --- Wishlist Action Buttons --- */
.wishlist-actions {
  display: flex;
  gap: 0.5rem;
}

/* Base style for both buttons */
.wishlist-btn {
  appearance: none;
  border: none;
  outline: none;
  font-size: 0.9rem;
  font-weight: 600;
  padding: 0.45rem 0.9rem;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s ease, transform 0.2s ease, color 0.2s ease;
}

/* Remove button */
.wishlist-remove-btn {
  background: #ef4444;       /* red-500 */
  color: #fff;
}
.wishlist-remove-btn:hover {
  background: #dc2626;       /* red-600 */
  transform: translateY(-1px);
}

/* View button (styled as a link but with button look) */
.wishlist-view-btn {
  background: #111827;       /* dark slate */
  color: #fff;
  text-decoration: none;
}
.wishlist-view-btn:hover {
  background: #1f2937;       /* slightly lighter dark slate */
  transform: translateY(-1px);
}
